import Vue from "vue";
import VueRouter from "vue-router";
import home from "../views/home.vue";
import photographers from "../components/photographers.vue";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: home,
  },
  {
    path: "/photographers",
    name: "photographers",
    component: photographers,
  },
];

const router = new VueRouter({
  routes,
});

export default router;
